<template>
  <div class="container">
    <span
      v-bind:class="{
        lost: !won,
        won,
      }"
    >
      {{ gameFinished ? (won ? "Won" : "Lost") : "\xa0"}}
    </span>
    <div class="status">
      <span class="bombs">
        <b>Bombs:</b> {{bombsTotal}}
      </span>
      <span class="flags">
        <b>Flags:</b> {{flaggedCount}}
      </span>
    </div>
    <div class="grid">
      <div class="line"
        :key="i"
        v-bind:style="{
          width: `calc(32px * ${columns})`,
        }"
        v-for="(line, i) in field"
      >
        <div
          :key="j"
          v-bind:class="{
            cellBombed: gameFinished && cell.bomb && cell.opened,
            cell: true,
            cellFlagged: cell.flagged,
            cellOpened: cell.opened,
            cellWon: gameFinished && cell.bomb && !cell.opened,
          }"
          v-for="(cell, j) in line"
          v-on:click.prevent.stop="leftClickHandler(i, j, $event)"
          v-on:contextmenu.prevent.stop="rightClickHandler(i, j, $event)"
        >
          <template v-if="cell.flagged">
            <Bomb v-if="gameFinished && cell.bomb" />
            <Flag v-else />
          </template>
          <template v-else-if="cell.opened || gameFinished">
            <Bomb v-if="cell.bomb" />
            <span v-else>
              {{ cell.adjacentBombs ? cell.adjacentBombs : "" }}
            </span>
          </template>
        </div>
      </div>
    </div>
    <div class="options">
      <div class="inputs">
        <div class="input">
          <label for="lines">Lines:</label>
          <input
            v-bind:value="lines"
            min="1"
            name="lines"
            ref="linesInput"
            step="1"
            type="number"
          />
        </div>
        <div class="input">
          <label for="columns">Columns:</label>
          <input
            v-bind:value="columns"
            min="1"
            name="columns"
            ref="columnsInput"
            step="1"
            type="number"
          />
        </div>
        <div class="input">
          <label for="bombsTotal">Bombs:</label>
          <input
            v-bind:value="bombsTotal"
            min="1"
            name="bombsTotal"
            ref="bombsTotalInput"
            step="1"
            type="number"
          />
        </div>
        <div class="buttons">
          <button
            class="reset"
            v-on:click.self.prevent.stop="resetGame($event, true)"
          >
            Reset current game
          </button>
          <button
            class="new"
            v-on:click.self.prevent.stop="resetGame"
          >
            New game
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { sync } from 'vuex-pathify';
  import Bomb from './Bomb.svg?inline';
  import Flag from './Flag.svg?inline';
  import getField from '../../utils/matriceGenerator';

  export default {
    components: {
      Bomb,
      Flag,
    },
    computed: {
      ...sync(['bombsTotal', 'columns', 'field', 'flaggedCount', 'gameFinished', 'lines', 'openedCount', 'won']),
    },
    methods: {
      checkIfGameFinished() {
        if (this.openedCount === this.lines * this.columns - this.bombsTotal) {
          this.won = true;
          this.gameFinished = true;
        }
      },
      clearAdjacentOpenStatus() {
        this.field.forEach((line, i) => {
          line.forEach((row, j) => {
            this.field[i][j].adjacentOpenStatus = false;
          });
        });
      },
      inputHandler($event, dataName) {
        this[dataName] = parseInt($event.target.value);
      },
      leftClickHandler(i, j) {
        if (this.gameFinished || this.field[i][j].opened || this.field[i][j].flagged) {
          return;
        }

        if (this.field[i][j].bomb) {
          this.won = false;
          this.gameFinished = true;
        }

        this.field[i][j].opened = true;
        this.openedCount++;

        this.openAdjacents(i, j);
        this.clearAdjacentOpenStatus();
        this.checkIfGameFinished();
      },
      openAdjacents(i, j) {
        if (this.gameFinished) {
          return;
        }

        if (this.field[i][j].adjacentOpenStatus) {
          return;
        }

        this.field[i][j].adjacentOpenStatus = true;

        if (this.field[i][j].bomb) {
          return;
        }

        if (!this.field[i][j].opened) {
          this.field[i][j].opened = true;
          this.openedCount++;
        }

        if (this.field[i][j].adjacentBombs > 0) {
          return;
        }

        for (let x = -1; x <= 1; x++) {
          for (let y = -1; y <= 1; y++) {
            if (x !== 0 || y !== 0) { //if not (i,j)
              if ((i + x >= 0) && (j + y >= 0)) { //if inside matrice
                if ((i + x < this.lines) && (j + y < this.columns)) { //if inside matrice
                  this.openAdjacents(i + x, j + y);
                }
              }
            }
          }
        }
      },
      resetField() {
        this.field.forEach((line, i) => {
          line.forEach((row, j) => {
            this.field[i][j].adjacentOpenStatus = false;
            this.field[i][j].opened = false;
            this.field[i][j].flagged = false;
          });
        });
      },
      resetGame($event, sameGame = false) {
        if (!sameGame) {
          this.lines = parseInt(this.$refs.linesInput.value) || 8;
          this.columns = parseInt(this.$refs.columnsInput.value) || 10;
          this.bombsTotal = parseInt(this.$refs.bombsTotalInput.value) || 10;

          this.field = getField(this.lines, this.columns, this.bombsTotal);
        } else {
          this.resetField();
        }
        this.flaggedCount = 0;
        this.openedCount = 0;
        this.gameFinished = false;
      },
      rightClickHandler(i, j) {
        if (this.gameFinished || this.field[i][j].opened) {
          return;
        }

        if (!this.field[i][j].flagged) {
          if (this.flaggedCount < this.bombsTotal) {
            this.field[i][j].flagged = true;
            this.flaggedCount++;
          }
        } else {
          this.field[i][j].flagged = false;
          this.flaggedCount--;
        }

        this.checkIfGameFinished();
      },
    },
    name: 'Minesweeper',
    props: {
      msg: String,
    },
  }
</script>

<style lang="scss" scoped>
  @import 'Minesweeper';
</style>
